
.paper>.head {
  padding: 1rem;
}
.paper>.head>h1 {
	line-height: 1.6em;
  margin-bottom: .75rem;
  font-size: 20px;
  font-weight: 400;
}
.paper>.head>p {
  font-size: 14px;
  color: #888;
}
.paper h2{
  font-size: 18px;
  color: #333;
  text-align: center;
  margin: 20px auto 10px;
  padding: 0 1rem .5rem;
  font-weight: normal;
}
.paper .weui-cells__title{
	font-size: 16px;
}
.paper .weui-cells{
	font-size: 14px;
}
.paper .weui-cell__ft {
  text-align: left;
}
.paper .datepicker>div{
	width: 105px;
}
.paper .vux-x-input.weui-cell.big .weui-label{
	width: 105px !important;
}
.paper .vux-x-input.weui-cell.long .weui-label{
	width: 150px !important;
}



